import './App.css';
import React, { useRef } from 'react'
import HeaderComponent from './Header/Header.js';
import { FaArrowDown } from "react-icons/fa";


function App() {

  const aboutRef = useRef()
  function toAbout() {
    aboutRef.current.scrollIntoView({ behavior: 'smooth' })
  }

  const projectsRef = useRef()
  function toProjects() {
    projectsRef.current.scrollIntoView({ behavior: 'smooth' })
  }

  const contactRef = useRef()
  function toContact() {
    contactRef.current.scrollIntoView({ behavior: 'smooth', block: "start" })
  }

  return (
      <div className="App">
        <HeaderComponent />
      <section>
          <img src="https://i.ibb.co/cv8ctvr/Matt-Profile-Img.jpg" alt="Matt" className="Profile-big"></img>
          <h1>This is a message about me</h1>
          <div className="Button-test" onClick={toAbout} style={{marginBottom: 60}}>About Me <div className="Arrow"> <FaArrowDown /></div></div>
        </section>
        <section id="about" ref={aboutRef}>
            <h1>Bio</h1>
            <div className="Button-test" onClick={toProjects}>My Projects <div className="Arrow"><FaArrowDown /></div></div>
        </section>
        <section id="projects" ref={projectsRef}>
            <h1>Projects</h1>
            <div className="Button-test" onClick={toContact}>Contact Me <div className="Arrow"><FaArrowDown /></div></div>
        </section>
        <section id="contact" ref={contactRef}>
          <h1>Contact</h1>
        </section>
      </div>
  );
}

export default App;
